import React, { Component } from 'react'
import './NewsItem.css'
import logo from './logo.png'
export default class NewsItem extends Component{
    clicked=()=>{
        console.log("yes")
    }
    render(){
        return(

            <div className="card">
                <div className="imgcontain">
                <img src={this.props.imgurl?this.props.imgurl:logo} alt="" />
                </div>
                <div className="container">
                    <h2>{this.props.title}</h2>
                    <p>{this.props.description}</p>
                    <p>By {this.props.author?this.props.author:"Unknown"} on {this.props.published?this.props.published:""}</p>
                    <div>
                    <button className='readMoreBtn' onClick={this.clicked}>Read More</button>
                    </div>
                </div>
            </div>
           
        );
    }
}