import React, { Component } from "react";
import NewsItem from "./NewsItem.js";
import "./News.css";
import Spinner from "./Spinner.js";
import PropTypes from "prop-types";
export default class News extends Component {
  static defaultProps = {
    country: "in",
    pageSize: 20,
  };
  static propTypes = {
    country: PropTypes.string,
    loading: PropTypes.bool,
    page: PropTypes.number,
    pageSize: PropTypes.number,
  };
  handlePrevious = async () => {
    this.setState({ page: this.state.page - 1, loading: true });
    document.documentElement.scrollTop = 0;
    this.updateNews();
  };
  handleNext = async () => {
    this.setState({ page: this.state.page + 1, loading: true });
    document.documentElement.scrollTop = 0;
    this.updateNews();
  };
  updateNews = async () => {
    this.props.setProgress(10);
    let url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=73b83e92b54b4155a2416bada75369e9&page=${this.state.page}&pageSize=${this.state.pageSize}`;
    let data = await fetch(url);
    let parsedData = await data.json();
    this.props.setProgress(50);
    this.setState({
      articles: parsedData.articles,
      loading: false,
      totalresults: parsedData.totalresults,
    });
    this.props.setProgress(100);
  };
  constructor() {
    super();
    this.state = {
      articles: [],
      page: 1,
      articlesCovered: 9,
      loading: false,
    };
  }
  async componentDidMount() {
    this.updateNews();
  }
  render() {
    return (
      <div className="containerNews">
        {this.state.loading && <Spinner />}
        <div className="heading">
          <h1>
            <span className="spanColorB">That's</span>
            <span className="spanColorR">News</span> - Top Headlines
          </h1>
        </div>
        <div className="row">
          {!this.state.loading &&
            this.state.articles.map((element) => {
              return (
                <NewsItem
                  key={element.url ? element.url : " "}
                  title={element.title ? element.title.slice(0, 45) : ""}
                  description={
                    element.description ? element.description.slice(0, 80) : ""
                  }
                  imgurl={element.urlToImage}
                  published={element.publishedAt.slice(0, 10)}
                  author={element.author}
                />
              );
            })}
        </div>
        <div className="handlePages">
          <button disabled={this.state.page <= 1} onClick={this.handlePrevious}>
            Previous
          </button>
          <button
            disabled={
              this.state.page + 1 > Math.ceil(this.state.totalresults / 20)
            }
            onClick={this.handleNext}
          >
            Next
          </button>
        </div>
      </div>
    );
  }
}
